<template>
  <v-main
    style="background: linear-gradient(to right, #002832 0%, #000032 100%)"
  >
    <div class="pt-12"></div>
    <v-card
      class="mx-auto"
      width="90%"
      outlined
      elevation="0"
      style="background-color: transparent"
    >
      <v-stepper
        v-model="e1"
        dark
        alt-labels
        style="background-color: transparent"
      >
        <v-stepper-header>
          <v-stepper-step complete-icon="mdi-check" :complete="e1 > 1" step="1">
            Stored amount
          </v-stepper-step>

          <v-divider></v-divider>

          <v-stepper-step complete-icon="mdi-check" :complete="e1 > 2" step="2">
            Check order
          </v-stepper-step>
        </v-stepper-header>

        <v-stepper-items>
          <v-stepper-content class="text-center" step="1">
            <p class="text-h3 green--text text--accent-2">DEPOSIT</p>
            <div class="pt-4"></div>
            <div class="line-green mx-auto"></div>
            <div class="pt-8"></div>
            <div class="div-input text-center mx-auto">
              <p class="text-h5 green--text text--accent-2">
                STORED VALUE AMOUNT
              </p>
              <div class="div-field mx-auto">
                <v-text-field
                  dark
                  outlined
                  dense
                  v-model="money"
                  required
                  clearable
                  color="green accent-2"
                  label="Input value"
                >
                </v-text-field>
              </div>
              <p class="text-h5 green--text text--accent-2">Transport :</p>
              <span class="text-h5 white--text">{{ transport }}</span>
              <div class="pt-4"></div>
              <p class="text-h5 green--text text--accent-2">WALLET ADDRESS :</p>
              <span class="text-h5 white--text">{{ walletAddress }}</span>
            </div>
            <div class="pt-16"></div>
            <div class="pt-8"></div>
            <v-card-actions>
              <v-btn
                class="ml-auto"
                color="primary"
                :disabled="depositNextModal"
                :loading="depositNextModal"
                @click="toCheckOrder()"
              >
                Continue
              </v-btn>
            </v-card-actions>
          </v-stepper-content>

          <v-stepper-content class="text-center" step="2">
            <p class="text-h3 green--text text--accent-2">ORDER</p>
            <div class="pt-4"></div>
            <div class="line-green mx-auto"></div>
            <div class="pt-8"></div>
            <div class="div-input text-center mx-auto">
              <div class="div-field mx-auto">
                <p class="text-h5 green--text text--accent-2">
                  YOUR STORED VALUE AMOUNT :
                </p>
                <span class="text-h5 white--text">{{ money }}</span>
              </div>
              <div class="pt-4"></div>
              <p class="text-h5 green--text text--accent-2">OUR ADDRESS :</p>
              <span class="text-h5 white--text">{{ masterWallet }}</span>
            </div>
            <div class="pt-16"></div>
            <div class="pt-8"></div>
            <v-card-actions>
              <div class="ml-auto">
                <v-btn
                  color="primary"
                  class="mr-8"
                  :disabled="checkModal"
                  :loading="checkModal"
                  @click="checkedOrder()"
                >
                  Continue
                </v-btn>
                <v-btn color="red" @click="e1 = 1"> Cancel </v-btn>
              </div>
            </v-card-actions>
          </v-stepper-content>
        </v-stepper-items>
      </v-stepper>
    </v-card>

    <div class="text-center">
      <v-dialog
        class="div-field"
        v-model="depositNextModal"
        hide-overlay
        persistent
      >
        <v-card
          class="text-center error-card d-flex align-center"
          width="auto"
          height="100"
          style="background-color: #000000"
        >
          <v-card-text class="text-h6 white--text pa-0 px-2">
            Please enter the amount of stored value first
          </v-card-text>
        </v-card>
      </v-dialog>
    </div>

    <div class="text-center">
      <v-dialog class="div-field" v-model="checkModal" hide-overlay persistent>
        <v-card
          class="text-center error-card d-flex align-center"
          width="auto"
          height="100"
          style="background-color: #000000"
        >
          <v-card-text class="text-h6 white--text pa-0 px-2">
            Order has been sent
          </v-card-text>
        </v-card>
      </v-dialog>
    </div>

    <div class="pt-16"></div>
  </v-main>
</template>

<script>
import { checkedOrder, remitAccount } from "../api";

export default {
  name: "Deposit",
  data() {
    return {
      e1: 1, //tab頁數
      money: 0,
      depositNextModal: false,
      checkModal: false,
      transport: "",
      walletAddress: "",
      masterWallet: "",
    };
  },
  created() {
    this.$store.dispatch("getUserData");
    this.transport = this.Wallet[0];
    this.walletAddress = this.Wallet[1];

    remitAccount().then((result) => {
      this.masterWallet = result.data.account;
    });
  },
  computed: {
    Wallet() {
      return this.$store.state.wallet.split(",");
    },
  },
  methods: {
    //儲值金額輸入後進到確認購買訂單
    toCheckOrder() {
      if (
        this.money == 0 ||
        this.money == "" ||
        this.money == null ||
        this.money == "undefined"
      ) {
        this.depositNextModal = true;
        setTimeout(() => (this.depositNextModal = false), 2000);
      } else {
        this.e1 = 2;
      }
    },

    //確認訂單後執行匯款api
    checkedOrder() {
      var params = new URLSearchParams();
      params.append("Money", this.money);

      checkedOrder(params)
        .then((result) => {
          if (result.data.success == true) {
            this.checkModal = true;
            setTimeout(() => {
              this.checkModal = false;
              location.reload();
            }, 2000);
          } else {
            alert(result.data.error_msg);
          }
        })
        .catch((err) => {
          console.error(err);
        });
    },
  },
};
</script>

<style lang="scss">
.error-card {
  background-color: #000000;
  opacity: 0.9;
}

.line-green {
  height: 1px;
  width: 90%;
  border: 1px solid #69f0ae;
}

.div-input {
  width: 90%;
}

.div-field {
  width: 40%;
}

@media screen and (max-width: 600px) {
  .div-field {
    width: 100%;
  }
}
</style>